@import url('https://fonts.googleapis.com/css?family=& display=swap');


body 
{
  font-family:'Nunito', sans-serif;
  background-color: #0059ff;
}
#quote-box{
  background: #fff;
  width:40rem;
  margin: 10rem auto;
  padding: 3rem;
  border-radius: 5px;
  }
#text{
  font-size: 2rem;
  margin-bottom: 1.5rem;

}
#author
{
  font-size: 1.2rem;
  display: flex;
  justify-content: flex-end;
  }

  #button  {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 2rem;
    float:left;
  }
  .social-media{
    display: flex;
  }
  .social-media > a
   {
    margin-right: .5rem;
    background-color: #0059ff;
    cursor:pointer;
      }

  .social-media > a > img  {
    width: 1.5rem;
  }
 
#new-quotes {
background-color: #0059ff ;
border: none;
color: #fff;
padding: .7rem .1rem;
font-size: .9rem;
font-weight: bold;
border-radius:4px;
outline: none;
cursor: pointer;
align-items: center;
}
#new-quotes:hover {
opacity: 0.1;
}
